* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    text-decoration: none;

}

body {

    color: #ffffff;
    background: #D9D9D9;
}
body, input, button, textarea{
    font: 300 18px Ubuntu, sans-serif;
}

#page-landing {
  font-family: "Raleway", sans-serif;
  width: 100vw;
  height: 100vh;
  background: #281231;
  color: #ffffff;
  display: flex;
  justify-content: center;
  align-items: center;
}
#page-landing .content-wrapper {
  width: 100%;
  max-width: 1100px;
  height: 100%;
  max-height: 680px;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
}

.content-wrapper .ct_login {
  font-size: 20px;
  line-height: 34px;
  display: flex;
  flex-direction: column;
  text-align: left;
}

.content-wrapper .ct_login p {
  margin: 0 0 -10px 0;
  padding: 0 0 0 68px;
  font-style: normal;
  font-weight: bold;
  font-size: 21px;
  line-height: 29px;
}
.content-wrapper .ct_login strong {
  font-weight: 700;
}

.content-wrapper .logo_login {
  padding: 0 0 48px 0;
}

.content-wrapper .logo_login input {
  width: 262px;
}
.content-wrapper .enter-app {
  margin: 16px 0 0 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: background-color 0.4s;
  width: 276px;
  height: 60px;
  background: #bb6bd9;
  border-radius: 36px;
  color: #ffffff;
}
.content-wrapper .enter-app:hover {
  box-shadow: inset 0px 4px 0px rgba(0, 0, 0, 0.25);
}
input {
  height: 47px;
  width: 262px;
  border: 1px solid #003770;
  background: #ffffff;
  box-shadow: inset -2px 2px 0px rgba(0, 0, 0, 0.25);
}
.new-image {
  height: 44px;
  background: #f5f8fa00;
  border-radius: 20px;
  cursor: pointer;
  padding: 35px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.fb {
  background-color: #4267b2 !important;
}

.sp {
  background-color: #1db954 !important;
}

.buttons {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.texto {
  margin: 18px;
  font-family: Raleway;
  font-style: normal;
  font-weight: 900;
  font-size: 32px;
  line-height: 38px;
  text-align: right;
}

.enter-app p {
  font-family: Raleway;
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  text-align: center;
}

