.word {
  position: relative;
  white-space: nowrap;
  color: white;
}

.word::after {
  content: attr(data-text);
  position: absolute;
  left: 0;
  top: 0;
  color: #6AEE3C;
  overflow: hidden;
  width: 100%;
  -webkit-animation: run-text 5s;
          animation: run-text 5s;
  -webkit-animation-fill-mode: backwards;
          animation-fill-mode: backwards;
  -webkit-animation-delay: 1s;
          animation-delay: 1s;
}

